import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {NgxQRCodeModule} from '@techiediaries/ngx-qrcode';

import {SharedModule} from 'app/shared/shared.module';
import {SqsrmyyRouter} from 'app/c-sqsrmyy/sqsrmyy-router';
import {OutpatientBillingComponent} from './outpatient-billing/outpatient-billing.component';
import {PaymentResultComponent} from 'app/c-sqsrmyy/payment-result/payment-result.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    NgxQRCodeModule,
    SqsrmyyRouter
  ],
  declarations: [
    OutpatientBillingComponent,
    PaymentResultComponent
  ]
})
export class CSqsrmyyModule {
}



